/* 0. Reset ================================= */
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* 1. Main Layout ================================= */
* {
	box-sizing: border-box;
}

body {
	background: white;
	font-family: sans-serif;
}

/* 2. Content ================================= */
.content video {
	width: 100%;
	line-height: 150%;
	max-height: 90vh;
}

.content .video-controls {
	text-align: center;
}

.content .video-controls button {
	padding: 10px 15px;
	border: 0;
	border-radius: 5px;
	background: #ddd;
	cursor: pointer;
}

.content .video-controls button:hover {
	background: #bbb;
}

@keyframes pulse-green {
	0% {
		background-color: #ddd;
	}
	50% {
		background-color: #c3f9c2;
	}
	100% {
		background-color: #ddd;
	}
}

@keyframes pulse-red {
	0% {
		background-color: #ddd;
	}
	50% {
		background-color: #f9aeae;
	}
	100% {
		background-color: #ddd;
	}
}

.content .video-controls button.play-pause.active {
	animation-name: pulse-green;
	animation-duration: 1s;
	animation-timing-function: ease-in-out;
	animation-iteration-count: infinite;
}

.content .video-controls button.mute-unmute.active {
	animation-name: pulse-red;
	animation-duration: 1s;
	animation-timing-function: ease-in-out;
	animation-iteration-count: infinite;
}

.content .video-controls p {
	margin: 5px 0;
}

.content .video-controls .progress {
	width: 100%;
	height: 10px;
	background-color: #ccc;
}

.content .video-controls .progress .progress-bar {
	background-color: #c20000;
	width: 0%;
	height: 100%;
}
